@import "src/styles/global.scss"; @import "portal/global.scss";



















































.demo-color-picker {
    .box {
        width: 300px;
        height: 30px;
    }

    .hue {
        background: linear-gradient(90deg, red 0, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, red);
    }

    .hsl {
        background-color: hsl(360, 100%, 40%);
    }
}

